* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --nav-color: #ffd269;
  --img-size: 200px;
}

#no-overflow {
  overflow-y: hidden;
}

.none {
  display: none !important;
}

nav {
  background-color: var(--nav-color);
  gap: 30px;
  padding: 20px;
  display: flex;
}

#filtres {
  position: relative;
}

#ingredients {
  cursor: pointer;
}

#ingredients-finding {
  order: -2;
  flex-flow: row no-wrap;
  justify-content: space-evenly;
  width: 100%;
  display: flex;
}

#search-ingredients {
  background-color: var(--nav-color);
  border: none;
  border-radius: 10px;
  outline: none;
  width: 70%;
  padding: 0 15px;
}

#search-ingredients:focus {
  border: none;
  outline: none;
}

#apply {
  cursor: pointer;
  background-color: var(--nav-color);
  border-radius: 10px;
  padding: 10px;
  display: block;
}

#apply:hover {
  background-color: #d69600;
}

#ingredients-div {
  background-color: #fff;
  flex-flow: wrap;
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: 50%;
  max-height: 300px;
  padding: 10px;
  display: flex;
  position: absolute;
  top: 4rem;
  overflow-y: scroll;
}

.ingredient {
  text-align: center;
  cursor: pointer;
  background-color: var(--nav-color);
  text-transform: capitalize;
  border-radius: 10px;
  flex-grow: 1;
  padding: 10px;
  display: inline;
}

.ingredient.selected {
  background-color: #d69600;
  order: -1;
}

#loading {
  z-index: 5;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

#data {
  flex-flow: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 2rem;
  width: 80%;
  margin: 5rem auto;
  display: flex;
}

#data img {
  width: var(--img-size);
  height: var(--img-size);
  aspect-ratio: 16 / 9;
  object-fit: contain;
  margin-bottom: 10px;
}

#data h1 {
  width: var(--img-size);
  text-align: center;
  overflow-wrap: word-wrap;
  font-size: 1.7rem;
}

#data p {
  text-align: center;
  font-size: .8rem;
}

.coctail {
  cursor: pointer;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5px;
  display: inline-flex;
}

#data div:hover > img {
  opacity: .7;
}
